import React, { useEffect, useState } from 'react'
import './style.css'
import { userActiveSubLevelDetailsStore } from '../../../../store/ActiveSubLevelDetailsStore'
import Video from 'components/Game/Screens/SubLevel/Video'
import Quiz from 'components/Game/Screens/SubLevel/Quiz'
import Activity from 'components/Game/Screens/SubLevel/Activity'
import Didyouknow from 'components/Game/Screens/SubLevel//Didyouknow'

const SubLevel = (props: any) => {

  const { aniout, setaniout } = props;

  const { ActiveSubLevelDetails, loadActiveSubleveldetails } = userActiveSubLevelDetailsStore();

  useEffect(() => {

    setaniout(false)

    if (!ActiveSubLevelDetails) {
      loadActiveSubleveldetails(localStorage.getItem("ActiveSubLevel"))
    }
  }, [])

  return (
    <>
      {(ActiveSubLevelDetails) && ActiveSubLevelDetails.sl_types === "VIDEO" &&
        <Video {...ActiveSubLevelDetails} aniout={aniout}/>}

      {(ActiveSubLevelDetails) && ActiveSubLevelDetails.sl_types === "QUIZ" &&
        <Quiz {...ActiveSubLevelDetails} aniout={aniout}/>}

      {(ActiveSubLevelDetails) && ActiveSubLevelDetails.sl_types === "ACTIVITIES" &&
        <Activity {...ActiveSubLevelDetails} aniout={aniout}/>}

      {(ActiveSubLevelDetails) && ActiveSubLevelDetails.sl_types === "DID YOU KNOW" &&
        <Didyouknow {...ActiveSubLevelDetails} aniout={aniout}/>}
    </>
  )
}
export default SubLevel;